import { Trans } from '@lingui/macro'
import { AddCircle, Delete } from '@material-ui/icons'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { SizerButton } from '../../../atoms/SizerButton'
import { FWeekdayFragment } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Radiuses } from '../../../styling'
import { BusinessHoursTimeRange } from '../atoms/BusinessHoursTimeRange'
import { TBusinessHour } from '../businesshours.types'

interface IProps {
  weekday: FWeekdayFragment
  hours: RoA<TBusinessHour>
  onAdd(): void
  onRemove(hourId: ID): void
}

const StyledBusinessHoursWeekday = styled.article`
  display: flex;
  align-items: baseline;
  margin: 0 0 0.3rem 0;
  h5 {
    width: 5rem;
  }
  & .range {
    display: flex;
    margin: 0 2rem 0 0;
    padding-right: 1rem;
    background: ${Colors.white};
    border-radius: ${Radiuses.Button};
  }
  .remove-btn {
    margin-right: 1rem;
  }
`

export const BusinessHoursWeekday = React.memo<IProps>(
  ({ weekday, hours, onAdd, onRemove }) => {
    const renderHour = (hour: TBusinessHour) => {
      return (
        <div key={hour.id} data-testid="bh-range" className="range">
          <SizerButton className="remove-btn">
            <TouchButton
              title={i18n.t`Remove business hour`}
              role="button"
              onClick={() => onRemove(hour.id)}
            >
              <Delete />
            </TouchButton>
          </SizerButton>
          <BusinessHoursTimeRange hour={hour} />
        </div>
      )
    }

    const renderHours = () => {
      if (hours.length === 0) {
        return (
          <em>
            <Trans>Closed</Trans>
          </em>
        )
      }
      return hours.map(renderHour)
    }

    return useObserver(() => (
      <StyledBusinessHoursWeekday>
        <h5>{weekday.code}</h5>
        {renderHours()}
        <SizerButton>
          <TouchButton
            title={i18n.t`Add business hour`}
            role="button"
            onClick={onAdd}
          >
            <AddCircle />
          </TouchButton>
        </SizerButton>
      </StyledBusinessHoursWeekday>
    ))
  },
)
