import { Trans } from '@lingui/macro'
import { XFieldName } from '@speedlo/xform'
import React from 'react'
import styled from 'styled-components'

import { FormInputTime } from './FormInputTime'

interface IFormInputTimeSpanProps {
  fieldFrom: XFieldName
  fieldTo: XFieldName
}

const InlineWrap = styled.div`
  width: 5rem;
  display: inline-block;
`

export const FormInputTimeSpan: React.FC<IFormInputTimeSpanProps> = ({
  fieldFrom,
  fieldTo,
}) => (
  <>
    <InlineWrap>
      <FormInputTime
        fieldName={fieldFrom}
        label={<Trans id="input.timespan.from">from</Trans>}
        fullWidth={false}
      />
    </InlineWrap>
    <InlineWrap>
      <FormInputTime
        fieldName={fieldTo}
        label={<Trans id="input.timespan.to">to</Trans>}
        fullWidth={false}
      />
    </InlineWrap>
  </>
)
