import { Trans } from '@lingui/macro'
import React from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRow } from '../../../atoms/FlexRow'
import { FSpecificBusinessHourFragment } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { TouchButtonDelete } from '../../../molecules/TouchButtonDelete'

interface ISpecificBusinessHourLineProps {
  bsHour: FSpecificBusinessHourFragment
  canDelete: boolean
  onDelete(): void
}

export const SpecificBusinessHourLine: React.FC<
  ISpecificBusinessHourLineProps
> = ({ bsHour, canDelete, onDelete }) => (
  <FlexRow>
    <FlexColumn fixedWidth>
      {canDelete && (
        <TouchButtonDelete
          onDelete={onDelete}
          confirmText={`${i18n.t`Really remove specific business hour ${bsHour.specificDateLocalized}?`}`}
        />
      )}
    </FlexColumn>
    <FlexColumn fixedWidth width="14rem">
      {bsHour.specificDateLocalized}
    </FlexColumn>
    <FlexColumn fixedWidth width="10rem">
      {`${bsHour.openAt} - ${bsHour.closeAt}`}
    </FlexColumn>
    <FlexColumn fixedWidth width="10rem">
      {bsHour.closed ? <Trans>Closed</Trans> : <Trans>Open</Trans>}
    </FlexColumn>
    <FlexColumn widthGrowIndex={1}>{bsHour.closedNote}</FlexColumn>
  </FlexRow>
)
