import { Trans } from '@lingui/macro'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { InputTime } from '../../../atoms/InputTime'
import { TBusinessHour } from '../businesshours.types'

interface IProps {
  hour: TBusinessHour
}

const StyledBussinessHoursTimeRange = styled.div`
  & input {
    width: 5rem;
  }
`

export const BusinessHoursTimeRange = React.memo<IProps>(({ hour }) => {
  return useObserver(() => (
    <StyledBussinessHoursTimeRange>
      <InputTime
        value={hour.openAt}
        onChange={value => (hour.openAt = value)}
        label={<Trans id="input.timespan.from">from</Trans>}
        fullWidth={false}
      />
      <InputTime
        value={hour.closeAt}
        onChange={value => (hour.closeAt = value)}
        label={<Trans id="input.timespan.to">to</Trans>}
        fullWidth={false}
      />
    </StyledBussinessHoursTimeRange>
  ))
})
