import { Trans } from '@lingui/macro'
import { idPropEq } from '@speedlo/tools'
import { isBefore, startOfDay } from 'date-fns/esm'
import produce from 'immer'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { MarginBox } from '../../../atoms/MarginBox'
import { useRoot } from '../../../atoms/RootProvider'
import {
  FRegularBusinessHourFragment,
  FSpecificBusinessHourFragment,
  QSpecificBusinessHoursQuery,
  useMSpecificBusinessHourDelete,
  useQSpecificBusinessHours,
} from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { useCanMutate } from '../../../hooks/useAccessGuard'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { SpecificBusinessHourLine } from '../molecules/SpecificBusinessHourLine'

type BsHour = FSpecificBusinessHourFragment

const sortByDate = RM.sort<BsHour>(
  // @ts-ignore Fix typing
  RM.ascend(RM.or(RM.prop('specificDate'), RM.not)),
)

const rejectOld = RM.reject<BsHour>(
  ({ specificDate }) =>
    Boolean(specificDate) && isBefore(specificDate!, startOfDay(new Date())),
)

export const SpecificBusinessHours = observer(() => {
  const { settings } = useRoot()

  const getBsHours = useLoadingCache<RoA<BsHour>>()

  const { data, loading, updateQuery } = useQSpecificBusinessHours({
    branchId: settings.branchId,
  })

  const bsHours = getBsHours(loading, () =>
    sortByDate(rejectOld(data.businessHours)),
  )

  const canDelete = useCanMutate('destroyBusinessHour')
  const [deleteBsHour] = useMSpecificBusinessHourDelete()

  if (bsHours === null) {
    return null
  }

  if (bsHours.length === 0) {
    return (
      <MarginBox margin={1}>
        <Trans>No specific business hours defined</Trans>
      </MarginBox>
    )
  }

  return (
    <>
      {bsHours.map(bsHour => (
        <SpecificBusinessHourLine
          key={bsHour.id}
          bsHour={bsHour}
          canDelete={canDelete}
          onDelete={async () => {
            await deleteBsHour({ id: bsHour.id })
            updateQuery(updateOnDelete(bsHour.id))
          }}
        />
      ))}
    </>
  )
})

function updateOnDelete(bsHour: ID) {
  return produce((draft: QSpecificBusinessHoursQuery) => {
    ;((draft.businessHours as unknown) as FRegularBusinessHourFragment[]).splice(
      draft.businessHours.findIndex(idPropEq(bsHour)),
      1,
    )
  })
}
