import { observer } from 'mobx-react-lite'
import React from 'react'

import { useCanQuery } from '../hooks/useAccessGuard'
import { NoAccess } from './NoAccess'

interface IProps {
  queryName: string
}

export const CanQuery = observer<IProps>(({ queryName, children }) => {
  const canQuery = useCanQuery(queryName)
  return canQuery ? (
    <>{children}</>
  ) : (
    <NoAccess>{`No access to query ${queryName}`}</NoAccess>
  )
})
