import { TimePicker, TimePickerProps } from '@material-ui/pickers'
import React from 'react'

import { DateTimeFormatProvider } from './DateTimeFormatProvider'

export const InputTime: React.FC<TimePickerProps & NoChildren> = props => {
  return (
    <DateTimeFormatProvider>
      <TimePicker format="LT" variant="inline" {...props} />
    </DateTimeFormatProvider>
  )
}
