import { DatePickerProps } from '@material-ui/pickers'
import { useXFieldForInput, useXFormState, XFieldName } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { InputDate } from '../../atoms/InputDate'

type TProps = NoChildren &
  Omit<DatePickerProps, 'value' | 'onChange'> & {
    fieldName: XFieldName
  }

export const FormInputDate = React.memo<TProps>(
  ({ fieldName, ...pickerProps }) => {
    const [field, getProps] = useXFieldForInput(fieldName)
    const formState = useXFormState()
    return useObserver(() => (
      <InputDate
        {...getProps()}
        onChange={field.setValue}
        onOpen={field.setTouched}
        disabled={formState.isSubmitting}
        {...pickerProps}
      />
    ))
  },
)
