import { Trans } from '@lingui/macro'
import { isoDate } from '@speedlo/graphql'
import { useNow } from '@speedlo/hooks'
import { XForm } from '@speedlo/xform'
import { endOfDay, setHours, setMinutes, startOfDay } from 'date-fns'
import { Observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { Sizer } from '../../../atoms/Sizer'
import { FormInput } from '../../../components/forms/FormInput'
import { FormInputDate } from '../../../components/forms/FormInputDate'
import { FormInputTimeSpan } from '../../../components/forms/FormInputTimeSpan'
import { FormSubmitButton } from '../../../components/forms/FormSubmitButton'
import {
  QSpecificBusinessHoursDocument,
  useMSpecificBusinessHourCreate,
} from '../../../graph/generated'
import { formatTime, fullDateFormat } from '../../../helpers/formats'
import { i18n } from '../../../i18n'
import { TSpecificBusinessHoursFormValues } from '../businesshours.types'
import { SpecificModeSelector } from '../molecules/SpecificModeSelector'

const DateContainer = styled.div`
  display: inline-block;
  width: 13rem;
  margin-left: 1rem;
  margin-top: 2rem;
`

const FormContainer = styled.div`
  display: inline-block;
  width: 16rem;
  min-height: 6rem;
  margin: 0rem 0 0 1rem;
  vertical-align: top;
`

type TFormValues = TSpecificBusinessHoursFormValues

export const SpecificBusinessHourForm: React.FC = () => {
  const getNow = useNow()
  const { settings } = useRoot()

  const [createBsHour] = useMSpecificBusinessHourCreate()

  const onSubmit = React.useCallback(
    (values: TFormValues) => {
      const openAt =
        settings.businessHours.isTimespanMode && values.openAt
          ? values.openAt
          : startOfDay(values.date)
      const closeAt =
        settings.businessHours.isTimespanMode && values.closeAt
          ? values.closeAt
          : endOfDay(values.date)
      createBsHour(
        {
          input: {
            companyBranch: settings.branchId,
            specificDate: isoDate(values.date),
            closed: !settings.businessHours.isTimespanMode,
            closedNote: values.note,
            openAt: formatTime(openAt),
            closeAt: formatTime(closeAt),
          },
        },
        {
          refetchQueries: [
            {
              query: QSpecificBusinessHoursDocument,
              variables: { branchId: settings.branchId },
            },
          ],
        },
      )
    },
    [createBsHour, settings],
  )

  return (
    <XForm onSubmit={onSubmit} initialValues={getInitialValues(getNow())}>
      <DateContainer>
        <FormInputDate
          fieldName="date"
          format={fullDateFormat}
          minDate={new Date()}
          fullWidth={true}
          label={<Trans>Date</Trans>}
        />
      </DateContainer>

      <FormContainer>
        <SpecificModeSelector />
        <Observer>
          {() =>
            settings.businessHours.isTimespanMode ? (
              <FormInputTimeSpan fieldFrom="openAt" fieldTo="closeAt" />
            ) : (
              <Sizer width={13}>
                <FormInput
                  fieldName="note"
                  fullWidth={true}
                  placeholder={i18n.t`Closing reason`}
                />
              </Sizer>
            )
          }
        </Observer>
      </FormContainer>

      <FormSubmitButton width="9rem">
        <Trans>Add</Trans>
      </FormSubmitButton>
    </XForm>
  )
}

function getInitialValues(now: Date): TFormValues {
  return {
    date: now,
    openAt: setHours(setMinutes(now, 0), 9),
    closeAt: setHours(setMinutes(now, 0), 20),
    note: '',
  }
}
